@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500;700);
@import url(https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alata);
body{font-size:62.5%}h1,h2,h3,h4,h5,h6{font-family:'Rubik', sans-serif;font-size:2rem;margin:0}body,p{font-family:'Alata', sans-serif}p{font-size:1rem}body{margin:0;overflow:hidden}.app-container{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;background:#e0e0e0;height:100vh}.app-container .main-column{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background:#1f3234;max-width:400px;padding-bottom:8px;font-size:1rem}.app-container .main-column h2{font-size:1.3rem;margin-bottom:10px}.app-container .main-column>div{margin:8px 8px 0 8px;background:#102021;border-radius:5px;padding:20px;color:#fff}.app-container .main-column>div h1,.app-container .main-column>div h2,.app-container .main-column>div h3,.app-container .main-column>div h4,.app-container .main-column>div h5,.app-container .main-column>div h6,.app-container .main-column>div p,.app-container .main-column>div span{color:#fff}.app-container .main-column .main-logo{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center}.app-container .main-column .main-logo .box-logo{background:#00eaff;width:35px;height:35px;border-radius:5px;margin-right:10px}.app-container .main-column .main-categories{-webkit-flex:1 1;flex:1 1}.app-container .list-column{width:100%;max-width:400px}.app-container .list-column ul{list-style:none;margin:0;padding:0}.app-container .list-column ul li{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;border-radius:10px;margin:8px 8px 0 8px;border:4px solid #102021;background:white}.app-container .list-column ul li .note-title-color{width:40px;background:#00ff84}.app-container .list-column ul li .note-title-box{margin-left:50px;padding:8px}.app-container .note-column{background:white;-webkit-flex:1 1;flex:1 1;margin:8px 8px 8px 0;border-radius:10px;border:4px solid #102021}.selected{background:red;color:white}

